body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: Roboto-medium;
  src: url('/fonts/roboto_mono_medium.ttf')
}

@font-face {
  font-family: Roboto-thin;
  src: url('/fonts/roboto_mono_thin.ttf')
}

.head-container {
  width: 100vw;
  height: 100vh;
  background-image: url('/img/hero.jpeg');
  background-repeat: no-repeat;
  background-position-y: 40%;
  background-size: cover;
}

.hero-title {
  font-size: 70px;
  margin-top: 0;
  font-family: 'Roboto-medium';
  color: rgba(228, 255, 47, 1);
}

.hero-content {
  margin: 0 auto;
  width: 50%;
  height: 20%;
  padding-top: 20%;
  text-align: center;
}

.hero-subtitle {
  font-size: 50px;
  margin-top: 0;
  font-family: 'Roboto-thin';
  color: rgba(228, 255, 47, 1);
}
body {
  background-color: #FF9EC2;
}

.lines {
  position: absolute;
  bottom: 0;
}

.shitstorm {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  padding: 0;
}
